$red: #f2511b;
$cream: #f1f1f1;

@mixin bungee-shade {
  font-family: 'Bungee Shade', cursive;
}

@mixin roboto {
  font-family: 'Roboto', sans-serif;
}

body {
  @include roboto;
  background-color: $cream;
  // margin: 20px;
}

h1 {
  @include bungee-shade;
  font-size: 40px;

  & a:hover {
    color: $red;
    text-decoration: none;
  }
}

a {
  color: $red;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

li {
  padding-left: 8px;

  &.gg {
    list-style-type: "\1F52A"; // knife emoji
  }

  &.lig {
    list-style-type: "\1F455"; // t-shirt emoji
  }

  &.astro {
    list-style-type: "\1F3AE"; // video game emoji
  }
}